jB,kBAAkB;QAClB,YAAY;IAChB;;IAEA;QACI,aAAa;QACb,SAAS;IACb;;IAEA;QACI,aAAa;QACb,SAAS;IACb;;;IAGA;QACI,kBAAkB;QAClB,aAAa;QACb,sBAAsB;QACtB,mBAAmB;QACnB,6BAA6B;QAC7B,kCAAkC;QAClC,WAAW;QACX,YAAY;QACZ,2CAA2C;QAC3C,iDAAiD;QACjD,kBAAkB;IACtB;;;IAGA;QACI,WAAW;QACX,YAAY;QACZ,uBAAuB;IAC3B;;IAEA;QACI,eAAe;QACf,mCAAmC;QACnC,YAAY;IAChB;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,0CAA0C;QAC1C,gBAAgB;IACpB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,SAAS;IACb;;IAEA;QACI,UAAU;IACd;;AAEJ","sourcesContent":["/* CSS Reset */\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n}\n\nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n    min-height: auto;\n    font-family: 'Montserrat', system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n    display: block;\n    max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    overflow-wrap: break-word;\n}\n\n#root,\n#__next {\n    isolation: isolate;\n}\n\n/* --- */\n\n@font-face {\n    font-family: 'Montserrat';\n    src: url('./fonts/Montserrat-VariableFont_wght.ttf') format('truetype');\n    font-weight: 400;\n    font-style: normal;\n}\n\n:root {\n    --off-white: hsl(0, 0%, 97%);\n    --light-green: hsl(120, 71%, 30%, 0.4);\n    --dark-green: rgb(22, 131, 22);\n}\n\n/* dialog */\n\ndialog {\n    animation: fade-out 0.5s ease-out;\n    align-self: center;\n    justify-self: center;\n    width: 70%;\n}\n\ndialog[open] {\n    animation: fade-in 0.5s ease-out;\n}\n\ndialog[open]::backdrop {\n    animation: backdrop-fade-in 0.5s ease-out forwards;\n}\n\n/* Animation keyframes */\n\n@keyframes fade-in {\n    0% {\n        opacity: 0;\n        transform: scaleY(0);\n        display: none;\n    }\n\n    100% {\n        opacity: 1;\n        transform: scaleY(1);\n        display: block;\n    }\n}\n\n@keyframes fade-out {\n    0% {\n        opacity: 1;\n        transform: scaleY(1);\n        display: block;\n    }\n\n    100% {\n        opacity: 0;\n        transform: scaleY(0);\n        display: none;\n    }\n}\n\n@keyframes backdrop-fade-in {\n    0% {\n        background-color: rgb(0 0 0 / 0%);\n    }\n\n    100% {\n        background-color: rgb(0 0 0 / 25%);\n    }\n}\n\n/* --- */\n\n#projectForm,\n#taskForm {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n}\n\nfieldset {\n    display: flex;\n    flex-direction: column;\n    border: none;\n}\n\n#projectForm input,\n#projectForm select,\n#projectForm textarea,\n#taskForm input,\n#taskForm select,\n#taskForm textarea {\n    margin-bottom: 0.5rem;\n}\n\nform legend {\n    font-size: larger;\n    font-weight: bold;\n    text-align: center;\n}\n\n#projectFormButtons,\n#taskFormButtons {\n    display: flex;\n    flex-direction: row;\n    align-items: flex-start;\n    justify-content: space-evenly;\n}\n\n.form-button {\n    font-size: 16px;\n    font-weight: bold;\n    border: none;\n    border-radius: 3px;\n    padding: 0.5rem 1.2rem;\n    cursor: pointer;\n    color: #FFFFFF;\n}\n\n.success-button {\n    background-color: #5995DA;\n}\n\n.success-button:hover {\n    background-color: #76AEED;\n}\n\n.success-button:active {\n    background-color: #407FC7;\n}\n\n.danger-button {\n    background-color: hsl(0, 100%, 60%);\n}\n\n.danger-button:hover {\n    background-color: hsl(0, 100%, 70%);\n}\n\n.danger-button:active {\n    background-color: hsl(0, 100%, 50%);\n}\n\n\n/* mobile nav */\nnav {\n    position: fixed;\n    bottom: 0;\n    height: 12vh;\n    display: flex;\n    align-items: center;\n    justify-content: space-evenly;\n    background-color: var(--off-white);\n    width: 100%;\n    border-top: 1px solid rgba(0, 0, 0, 0.15);\n    box-shadow: rgba(0, 0, 0, 0.15) 0px -10px 10px -10px;\n}\n\nnav button {\n    width: 100%;\n    height: 100%;\n    border: none;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    cursor: pointer;\n    background-color: inherit;\n}\n\nnav button svg {\n    width: 40px;\n    height: 40px;\n    fill: var(--dark-green);\n}\n\n#showTaskForm {\n    padding: 0.5rem;\n    background-color: var(--dark-green);\n    color: white;\n}\n\n#showTaskForm svg {\n    fill: white;\n}\n\n.activePage {\n    border-top: 5px solid var(--light-green);\n}\n\n.toggleHidden {\n    display: none;\n}\n\n/* --- */\n\n/* header */\n\n#header {\n    position: fixed;\n    top: 0;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n    height: 7vh;\n    background-color: var(--off-white);\n    border-bottom: 1px solid rgba(0, 0, 0, 0.15);\n    box-shadow: rgba(0, 0, 0, 0.15) 0px 10px 3px -8px;\n\n}\n\n#header h1 {\n    color: hsl(120, 71%, 30%);\n}\n\n/* --- */\n\n/* content */\n\n\n.TaskContent {\n    margin-top: 7vh;\n    margin-bottom: 12vh;\n    padding: 1rem;\n    display: grid;\n    grid-template-rows: repeat(auto-fill, minmax(60px, auto));\n    align-items: center;\n    overflow: auto;\n    gap: 1rem;\n    min-height: auto;\n}\n\n.ProjectList {\n    margin-top: 7vh;\n    margin-bottom: 12vh;\n    padding: 1rem;\n    display: grid;\n    grid-template-rows: repeat(auto-fill, minmax(82px, auto));\n    align-items: center;\n    overflow: auto;\n    gap: 1rem;\n    min-height: auto;\n}\n\n#showProjectForm {\n    font-size: 16px;\n    font-weight: bold;\n    border: none;\n    border-radius: 3px;\n    padding: 0.5rem 1.2rem;\n    cursor: pointer;\n    color: #FFFFFF;\n    background-color: var(--dark-green);\n}\n\n#showProjectForm:active {\n    background-color: var(--light-green);\n}\n\n\n\n/* ---- */\n\n/* Task card */\n\n.taskCard {\n    width: 100%;\n    padding: 1rem;\n    border: 1px solid var(--off-white);\n    box-shadow: rgba(0, 0, 0, 0.25) 0px 2px 8px;\n    border-radius: 1rem;\n    display: grid;\n    grid-template-columns: 1fr 6fr 1fr;\n    gap: 5px;\n    align-items: center;\n    height: 100%;\n\n    /* when deleting */\n    opacity: 1;\n    -webkit-transition: opacity 1000ms linear;\n    transition: opacity 1000ms linear;\n}\n\n.taskCard input[type='checkbox'] {\n    width: 25px;\n    height: 25px;\n    justify-self: center;\n}\n\n.taskCard p {\n    font-weight: bold;\n    overflow: auto;\n    font-size: large;\n    cursor: pointer;\n}\n\n.taskCard svg {\n    width: 2rem;\n    height: 2rem;\n}\n\n.checkedTitle {\n    text-decoration: 2px line-through;\n    opacity: 40%;\n}\n\n.priority_Urgent {\n    border-left: 4px solid red;\n}\n\n.priority_Important {\n    border-left: 4px solid yellow;\n}\n\n.priority_LowPriority {\n    border-left: 4px solid lightblue;\n}\n\n.deleteIcon {\n    fill: var(--dark-green);\n    cursor: pointer;\n}\n\n\n.deleteIcon:hover {\n    fill: hsl(0, 100%, 60%);\n}\n\n.deleteIcon:active {\n    fill: hsl(0, 100%, 50%);\n}\n\n/* --- */\n\n/* show task page  */\n\n.showTask {\n    margin-top: 7vh;\n    padding: 2rem;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.showTaskWrapper {\n    border: 1px solid var(--off-white);\n    box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;\n    padding: 1rem;\n    margin-top: 2rem;\n    width: 100%;\n}\n\n.showTaskHeader {\n    display: flex;\n    align-items: center;\n}\n\n.showTaskHeader h2 {\n    margin: 1rem;\n    word-break: break-word;\n}\n\n.showTaskHeader svg {\n    width: 3rem;\n    height: 3rem;\n    fill: var(--dark-green);\n    margin-right: 1rem;\n}\n\n.showTaskMain p {\n    margin-bottom: 0.5rem;\n}\n\n.showTaskFooter {\n    display: flex;\n    align-items: center;\n    justify-content: space-evenly;\n    margin-top: 1.5rem;\n}\n\n.showTaskCreatedDate {\n    opacity: 50%;\n}\n\n/* --- */\n\n/* project list page  */\n\n.uncheckedIcon,\n.checkedIcon {\n    fill: var(--dark-green);\n}\n\n.taskCounter,\n.projectCompleted {\n    border-radius: 80%;\n    border: 1px solid var(--light-green);\n    width: 3rem;\n    height: 3rem;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    font-weight: bold;\n}\n\n.ProjectPageTitle {\n    font-size: xx-large;\n}\n\n.projectCompleted {\n    display: none;\n}\n\n.taskCounter {\n    background: radial-gradient(var(--off-white), var(--light-green));\n}\n\n.pageHeader {\n    display: grid;\n    grid-template-columns: 6fr 1fr 1fr;\n    grid-template-rows: 1fr 1fr;\n    gap: 4px;\n    align-items: center;\n}\n\n.pageHeader h2,\n.pageHeaderToday h2 {\n    font-size: xx-large;\n}\n\n.DeleteIconPage,\n.EditIconPage {\n    width: 2rem;\n    height: 2rem;\n\n    fill: var(--dark-green)\n}\n\n.EditIconPage:hover {\n    fill: #76AEED;\n}\n\n.EditIconPage:active {\n    fill: #407FC7;\n}\n\n.DeleteIconPage:hover {\n    fill: hsl(0, 100%, 70%);\n}\n\n.DeleteIconPage:active {\n    fill: hsl(0, 100%, 50%);\n}\n\n/* --- */\n\n/* desktop */\n\n@media only screen and (min-width: 700px) {\n    #taskForm {\n        display: grid;\n        grid-template-columns: 1fr 1fr;\n        grid-template-rows: 1fr 5fr 1fr;\n    }\n\n    #taskForm legend {\n        grid-column: -1/1;\n    }\n\n    #taskFormButtons {\n        grid-column: -1/1;\n    }\n\n    #projectFormButtons {\n        display: flex;\n        flex-direction: row;\n        align-items: flex-start;\n        justify-content: space-evenly;\n    }\n\n    dialog {\n        width: 50%;\n    }\n\n    /* side nav */\n\n    body {\n        display: grid;\n        grid-template-columns: 1fr 5fr;\n        grid-template-rows: 1fr 9fr;\n        min-height: 100vh;\n        gap: 0;\n    }\n\n    #content {\n        min-width: 100%;\n        height: 90vh;\n        overflow: scroll;\n    }\n\n    #header {\n        position: relative;\n        grid-column: 1/-1;\n        text-align: center;\n        height: auto;\n    }\n\n    .TaskContent {\n        padding: 2rem;\n        margin: 0;\n    }\n\n    .ProjectList {\n        padding: 2rem;\n        margin: 0;\n    }\n\n\n    nav {\n        position: relative;\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        justify-content: space-evenly;\n        background-color: var(--off-white);\n        width: 100%;\n        height: 100%;\n        border-right: 1px solid rgba(0, 0, 0, 0.15);\n        box-shadow: rgba(0, 0, 0, 0.15) 10px 0px 3px -8px;\n        font-size: x-large;\n    }\n\n\n    nav button svg {\n        width: 40px;\n        height: 40px;\n        fill: var(--dark-green);\n    }\n\n    #showTaskForm {\n        padding: 0.5rem;\n        background-color: var(--dark-green);\n        color: white;\n    }\n\n    #showTaskForm svg {\n        fill: white;\n    }\n\n    .activePage {\n        border-right: 5px solid var(--light-green);\n        border-top: none;\n    }\n\n    .toggleHidden {\n        display: none;\n    }\n\n    .showTask {\n        margin: 0;\n    }\n\n    .showTaskWrapper {\n        width: 50%;\n    }\n\n}"],"sourceRoot":""}]);
